const currentSexForSearch = (currentSex: string): string => {
  if (
    currentSex.toLocaleLowerCase() === 'male' ||
    currentSex.toLocaleLowerCase() === ''
  ) {
    return 'Male';
  } else if (
    currentSex.toLocaleLowerCase() === 'female' ||
    currentSex.toLocaleLowerCase() === ''
  ) {
    return 'Female';
  }
  return 'Both sexes';
};

export default currentSexForSearch;
